:root {
  /*Blue, peach, pink, off-white*/
  --blue: #365367;
  --peach: #FFBAA2;
  --white: #F9F4E6;
  --pink: #F494AB;
}

html, body {
  font-family: 'Open Sans', sans-serif;
  text-align: center;
}


* {
  margin: 0;
  padding: 0;

}

#introduction {
  background-color: var(--blue);
}

.intro-wrapper {
  height: 100%;
  min-height: 55vh;
  padding-top: 45vh; 
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid red;
  /*   line-height: 100vh; */
  /*   width:100%;
  height:96vh;
border:solid; */
}
.blue { 
  color: var(--white);
      }
.peach {
  color: var(--peach);
}
.white {
  color: var(--white);
}
.pink {
  color: var(--pink);
}

.flex-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #CCC;
}




.flex-wrapper h1 {
  margin-top: 5px;
}




.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

